<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12 sm3>
        <v-parallax
        :src="require('@/assets/team.jpg')"
        height="600">
          <v-container fill-height>
            <v-layout align-top>
              <v-flex text-xs-center>
              </v-flex>
            </v-layout>
          </v-container>
        </v-parallax> 
      </v-flex>
      <v-flex xs12 sm9>
        <v-container fill-height>
          <v-layout>
            <v-flex>
              <h1>Founding Committee</h1>
              <h2 class="mt-4">Chamber of Commerce, Industry, Tourism and Integration Colombia Singapore</h2>
              <v-layout 
              row 
              wrap
              class="my-4">
                <v-flex
                  v-for="(member, index) in members"
                  :key="index"
                  xs12 sm6
                >
                  <v-card
                  class="ma-2">
                    <v-layout row>
                      <v-flex xs6>
                        <v-card-title primary-title>
                          <div>
                            <div class="headline">{{member.subtitle}}</div>
                            <h3>{{member.title}}</h3>
                            <div>{{member.job}}</div>
                          </div>
                        </v-card-title>
                      </v-flex>
                      <v-flex xs6>
                        <v-card-media
                          :src="require('@/assets/' + member.avatar + '.png')"
                          height="125px"
                          contain
                          class="my-2"
                        ></v-card-media>
                      </v-flex>
                    </v-layout>
                    <v-divider light></v-divider>
                    <v-card-actions class="pa-3">
                      {{member.email}}
                    </v-card-actions>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      members: [
        {
          avatar: 'mario',
          title: 'Mario Jose Lizana-Allende',
          subtitle: 'President',
          job: 'Managing Director, Exicon Investment Banking',
          email: 'mario@ccitics.com'
        },
        {
          avatar: 'cecil',
          title: 'Cecil Yi Peng Ang',
          subtitle: 'Secretary-General',
          job: 'Founder and CTO, Betafish Pte Ltd (Singapore)',
          email: 'cecil@ccitics.com'
        },
        {
          avatar: 'tata',
          title: 'Tatiana Oñate Acosta',
          subtitle: 'Attorney',
          job: 'Law Professor, University of Rosario (Colombia)',
          email: 't_onate@ccitics.com'
        }
      ]
    }
  }
}
</script>


