<template>
  <v-app>
    <v-navigation-drawer
      v-model="drawer"
      enable-resize-watcher
      app
    >
      <v-list>
        <v-select
        class="ma-3"        
        :items="languages"
        v-model="langInput"
        color="success"
        item-text="language"
        item-value="language"
        return-object
        v-on:change="changeRoute"
        label="Select language"
        ></v-select>
        <v-divider></v-divider>
        <v-list-tile
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link"
          class="dark--text"
        >
          <v-list-tile-action>
            <v-icon left>{{item.icon}}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title> {{item.title}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      fixed
      class="dark--text elevation-0"
      id="toolbar"
    >
      <v-toolbar-side-icon 
      class="hidden-sm-and-up"
      @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link
        v-if="!spanish"
        to="/" 
        tag="span" 
        style="cursor: pointer">
          <img src="@/assets/logo.png" style="height: 50px">
        </router-link>
        <router-link
        v-if="spanish"
        to="/es" 
        tag="span" 
        style="cursor: pointer">
          <img src="@/assets/logo.png" style="height: 50px">
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-select
        class="ma-3"
        :items="languages"
        v-model="langInput"
        color="success"
        item-text="language"
        item-value="route"
        return-object
        v-on:change="changeRoute"
        label="Select language"   
        ></v-select>
        <v-btn 
        flat 
        v-for="item in menuItems" 
        :key="item.title"
        :to="item.link"
        class="dark--text"
        >
          <v-icon left>{{ item.icon }}</v-icon>
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content class="mt-4 pt-5">
      <router-view/>
    </v-content>
    <v-footer class="ml-3 transparent" app>
      <span>&copy; CCITICS 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      drawer: false,
      languages: [
        {
          language: 'English',
          route: '/'
        },
        {
          language: 'Español',
          route: '/es'
        }
      ],
      langInput: ''
    }
  },
  // guard nav items according to user login status
  computed: {
    menuItems () {
      let menuItems = [
        {
          icon: 'group',
          title: 'Founding Committee',
          link: '/team'
        },
        {
          icon: 'mail',
          title: 'Contact Us',
          link: '/contact'
        }
      ]
      if (this.spanish) {
        menuItems = [
          {
            icon: 'group',
            title: 'Comité Organizador',
            link: '/es/equipo'
          },
          {
            icon: 'mail',
            title: 'Contáctenos',
            link: '/es/contacto'
          }
        ]
      }
      return menuItems
    },
    spanish () {
      return this.langInput.language === 'Español'
    }
  },
  methods: {
    changeRoute (a) {
      this.$router.push({ path: a.route })
    }
  },
  name: 'App'
}
</script>

<style scoped>
  #toolbar {
    opacity: 0.9;
  }
</style>



